import { makeStyles } from '@material-ui/core/styles';
import { pxToRem } from '../../../../styles/themes/vad/constants';

const useStyles = makeStyles(theme => {
  return {
    root: {
      position: 'relative',

      '& svg': {
        maxWidth: 'none',
        maxHeight: 'none',
        '& path': {
          stroke: theme.mixins.Black(),
        },
      },
      '& .gm-style-iw-c': {
        borderRadius: 0,

        '& > button': {
          display: 'none !important',
          opacity: 0,
        },
      },
      '& .gm-style .gm-style-iw-t::after': {
        background: 'transparent',
        boxShadow: 'none',
      },

      '& .baidu-infoWindow': {
        '& a': {
          textDecoration: 'none',
          fontSize: pxToRem(16),
        },
        '& h4': {
          fontFamily: theme.typography.fontFamily.primaryBold,
          marginBottom: pxToRem(5),
        },
        '& p': {
          fontSize: pxToRem(16),
          marginBottom: pxToRem(8),

          [theme.breakpoints.up('lg')]: {
            fontSize: pxToRem(20),
          },
        },
        '& .MuiButton-root': {
          fontSize: 'inherit',
          color: theme.mixins.AbuDhabiRed(),
          marginLeft: pxToRem(-5),
          '&:hover': {
            backgroundColor: 'transparent',
          },
        },
        '& svg path': {
          stroke: 'none',
        },
      },
      '& #map_canvas': {
        backgroundColor: theme.palette.background.default,
      },
    },
    infoBox: {
      height: pxToRem(100),
    },
    holder: {
      position: 'relative',
      height: pxToRem(468),

      [theme.breakpoints.down('lg')]: {
        marginLeft: '-1.5rem',
        marginRight: '-1.5rem',
      },
      [theme.breakpoints.up('lg')]: {
        height: pxToRem(768),
      },
    },
    legends: {
      backgroundColor: theme.palette.common.white,
      padding: pxToRem(20),

      '&.overlay': {
        position: 'absolute',
        bottom: pxToRem(24),
        left: pxToRem(12),
        width: pxToRem(300),
        boxShadow: theme.shadows[1],
        padding: pxToRem(15),

        [theme.breakpoints.up('lg')]: {
          width: pxToRem(415),
          padding: pxToRem(20),
        },
      },
    },
    list: {
      paddingLeft: pxToRem(10),
      margin: 0,

      [theme.breakpoints.up('lg')]: {
        borderBottom: theme.borders.divider,
        paddingBottom: pxToRem(20),
        marginBottom: pxToRem(20),
      },
    },
    infoWindow: {
      maxWidth: pxToRem(415),
      padding: `${pxToRem(5)} ${pxToRem(10)}`,
    },
    address: {
      color: theme.palette.text.tertiary,
      fontSize: pxToRem(16),
    },
    directionBtn: {
      fontSize: pxToRem(16),
      marginTop: 0,
      marginLeft: '-5px',

      '& .MuiButton-startIcon': {
        padding: 0,
        marginLeft: 0,
      },

      '& svg path': {
        stroke: 'none',
      },
    },
  };
});

export default useStyles;
