import React, { useEffect } from 'react';
import get from 'lodash/get';
import { withSitecoreContext } from '@sitecore-jss/sitecore-jss-react';
import { withNamespaces } from 'react-i18next';

import withErrorBoundary from '../../../utils/HOCs/withErrorBoundary';
import Box from '../../../components/molecules/Box';
import useStyles from './style';
import MapViewer from '../../../components/molecules/MapViewer';
import Grid from '../../../components/molecules/Grid';
import Typography from '../../../components/atoms/Typography';
import { Text } from '@sitecore-jss/sitecore-jss-react';
import { getRequestBody } from '../SimilarSearch/preProcessData';
import { getHelperKeysFromDictionary } from '../../../../utils/utility';
import { evalConditionForInsideChina } from '../../../utils/cookie';
import { LANGUAGE_CODE, BAIDU_API_KEY, GOOGLE_API_KEY } from '../../../../constants';
import { COMPONENT_NAME } from './locators';
import { getComponentName, getPageName } from '../../../utils/getPageComponentInfo';
import { NoSsr } from '@material-ui/core';
import { getAPIKey } from '../../../utils/getApiKey';

export const dictionaryKeys = {
  baiduApiKey: BAIDU_API_KEY,
  googleApiKey: GOOGLE_API_KEY,
  GetDirections: 'GetDirections',
  viewFullItinerary: 'viewFullItinerary',
};

const BranchesMap = props => {
  const classes = useStyles(props);
  const componentName = getComponentName(props);
  const pageName = getPageName(props);
  //const [isMapLoaded, setIsMapLoaded] = useState(true);

  const { getOtherBranchesStart, otherBranchesData, t } = props;

  const sitecoreContext = get(props, 'sitecoreContext', []);
  const language = get(sitecoreContext, 'language', LANGUAGE_CODE.DEFAULT);

  const title = get(sitecoreContext, 'route.fields.heading', undefined);

  const helperKeys = getHelperKeysFromDictionary(t, dictionaryKeys);

  useEffect(() => {
    if (!otherBranchesData.length && get(sitecoreContext, 'language', '')) {
      getOtherBranchesStart(getRequestBody(props, 'Branches'));
    }
  }, [get(sitecoreContext, 'language', '')]);

  const branches = otherBranchesData.length
    ? otherBranchesData.map((branch, i) => {
      return {
        id: (i + 2).toString(),
        name: branch.name,
        lat: parseFloat(branch.latitude),
        lng: parseFloat(branch.longitude),
        address: branch.location,
      };
    })
    : [];

  let locations = [
    {
      id: '1',
      name: get(sitecoreContext, 'route.fields.name.value', ''),
      lat: parseFloat(get(sitecoreContext, 'route.fields.latitude.value')),
      lng: parseFloat(get(sitecoreContext, 'route.fields.longitude.value')),
      address: get(sitecoreContext, 'route.fields.location.value', ''),
    },
    ...branches,
  ];
  locations = locations.length > 0 && locations.filter(item => item && item.lat && item.lng);
  const apiKey = getAPIKey(language);

  // if (isMapLoaded && isLanguageEqual(language, LANGUAGE_CODE.CHINESE)) {
  //   fetch(modifyApi(BAIDU_MAP_API_URL, { apiKey }))
  //     .catch(err => {
  //       console.log("Map is not loading due to: ", err);
  //       setIsMapLoaded(false);
  //     });
  // }
  // if (!isMapLoaded) {
  //   return null;
  // }

  return (
    <Box className={classes.root} data-locator={COMPONENT_NAME}>
      <NoSsr>
        {!evalConditionForInsideChina(language) &&
          locations.length > 0 &&
          apiKey && <Grid>
            {title && (
              <Typography
                variant="h4"
                component="h2"
                className={classes.title}
              >
                <Text field={title} />
              </Typography>
            )}
            <MapViewer
              language={language}
              markers={locations}
              isRouteDirection={false}
              highlightFirstMarker={true}
              labels={helperKeys}
              apiKey={apiKey}
              componentName={componentName}
              pageName={pageName}
            />
          </Grid>}
      </NoSsr>
    </Box>
  );
};

export default withErrorBoundary(
  withSitecoreContext()(withNamespaces()(BranchesMap))
);
